#Order {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;

  height: 100%;
  background-color: white;
  padding: 60px 40px;
  
  height: 100%;
}

#order-items {
  overflow-y: scroll;
  top: 0px;
  right: 0px;
  width: 100%;
  height: 80%;
}

#Order ul {
  list-style: none;
}

#total {
  float: right;
}

#Order.side-bar {
  width: 25%;
}


